.navbar-container {
    .navbar {
        width: 100%;
        padding: 10px 0;
        background: #FFFFFF !important;
        z-index: 100;
    }
    .nav-link {
        font-family: 'InterUI-Regular';
        // font-size: 13px;
        color: #737374;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .nav-item {
        margin-right: 20px;
    }
    .devpartnersBrand {
        width: 165px;
    }
    .menuListContainer {
        text-align: right;
    }
    .lets-talk-container{
        cursor: pointer;
        background: #FFFFFF;
        border: 1px solid #F8B63E;
        border-radius: 5px;
        height: 100%;
        padding: 11px;
        a {
          font-family: 'InterUI-Medium';
          font-size: 13px;
          color: #F8B63E;
          text-align: center;
        }
    }
    .menu {
        display: none;
    }
    .mobileMenuButton {
        display: none;
    }
    .mobile-container-nav {
        padding: 0;
    }
    @media (max-width: 768px) {
        .mobile-container-nav {
            padding: 0 20px;
        }
        .mobileMenuButton {
            display: inline-block;
            background: #FFFFFF;
            padding: 5px 15px;
            border: none;
        }
        button.navbar-toggler {
            display: none;
        }
        .lets-talk-button-container {
            width: 100%;
            text-align: center;
            margin-top: 40px;
        .lets-talk-button  {
            padding-top: 20px;
            background: #F8B63E;
            border-radius: 5px;
            color: #FFFFFF;
            padding: 11px 125px;
            border: none;
        }
        }
        .push {
            -webkit-transition: all 0.3s ease;
            -moz-transition: all 0.3s ease;
            transition: all 0.3s ease;
        }
        .btn {
        border: none;
        background-color: #3498db;
        color: #fff;
        font-size: 16px;
        padding: 15px 0;
        display: block;
        width: 100%;
        margin: 10px 0;
        text-align: center;
        text-decoration: none;
        &:hover, &:active {
            background-color: #FFFFFF;
        }
        }
        .backBtn {
        background-color: #FFFFFF;
        font-size: 16px;
        text-align: right;
        &:hover {
            color: #000; 
        }
        }
        .menu-right .backBtn {
        text-align: left;
        }
        .menu-top .backBtn, .menu-bottom .backBtn {
        text-align: left;
        }
        h2 {
        text-align: center;
        font-size: 18px;
        font-weight: normal;
        margin: 15px 0;
        }
        .menu {
        border-right: 1px solid #FFFFFF;
        background-color: #FFFFFF;
        -webkit-transition: all 0.3s ease;
        -moz-transition: all 0.3s ease;
        transition: all 0.3s ease;
        position: fixed;
        display: inline;
        top: 0;
        z-index: 10;
        width: 350px;
        height: 100%;
        a {
            display: block;
            font-family: 'InterUI-Regular';
            font-size: 16px;
            color: #71748D;
            line-height: 30px;
            padding: 16px;
            border-top: 1px solid #FFFFFF;
            text-decoration: none;
            position: relative;
            z-index: 11;
            &:last-child {
            border-bottom: 1px solid #FFFFFF;
            }
            &:hover, &:active {
            background-color: #FFFFFF;
            }
        }
        a:first-of-type {
            margin-top: 20px;
        }
        }
        .menu-left {
        left: -350px;
        &.left-open {
            left: 0;
        }
        }
        .menu-right {
        right: -350px;
        margin-top: 60px;
        &.right-open {
            right: 0;
        }
        }
        .menu-top {
        width: 100%;
        top: -100%;
        &.top-open {
            top: 0;
        }
        }
        .menu-bottom {
        width: 100%;
        top: 100%;
        &.bottom-open {
            top: 0;
        }
        }
        
        .push {
        -webkit-transition: all 0.3s ease;
        -moz-transition: all 0.3s ease;
        transition: all 0.3s ease;
        }
        .push-left {
        left: -350px;
        &.pushleft-open {
            left: 0;
        }
        }
        .push-toleft {
        left: 350px;
        }
        .push-right {
        right: -350px;
        .backBtn {
            text-align: left;
        }
        &.pushright-open {
            right: 0;
        }
        }
        .push-toright {
        left: -350px;
        }
    }
}