.footerContainer {
  .footer-background {
    background: #222431 !important;
    padding-top: 50px;
  }
  .gold-text {
      color: #FFBB41 !important;
  }
  .footer-nav-text {
      color: #9B9FBD;
  }
  .company-name {
    font-family: 'InterUI-Regular';
    font-size: 14px;
    color: #FFFFFF;
    line-height: 30px;
  }
  .company-social {
    font-size: 28px;
  }
  .footer-divider {
    opacity: 0.1;
    border: 2px solid #E6E6E6;
    margin: 50px 0 40px 0;
  }
  .footer-divider:last-of-type {
    margin-bottom: 17px;
  }
  .recent-post-container {
    .cover-image {
      max-width: 125% !important;
    }
    .flex-container {
      height: 100%;
      padding: 0;
      margin: 0;
      display: -webkit-box;
      display: -moz-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      // align-items: center;
      // justify-content: center;
    }
    .flex-box-one {
      flex: 0.5;
      margin-right: 28px;
    }
    .flex-box-one:last-of-type {
      margin-right: 0;
    }
    .flex-box-two {
      flex: 2;
      .title-footer {
        font-size: 11px;
        color: #FFFFFF;
        font-family: 'InterUI-Medium';
      }
      .content-footer {
        font-family: 'InterUI-Regular';
        font-size: 11px;
      }
      .read-more-button {
        font-family: 'InterUI-Bold';
        font-size: 14px;
        color: #FFBB41;
        line-height: 21px;
      }
    }
    .title {
      font-family: 'InterUI-Bold';
      font-size: 18px;
      color: #FFFFFF;
      line-height: 24px;
    }
    .grid-container {
      max-width: 1141px;
      margin: 0 auto;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-auto-rows: 1fr;
      grid-column-gap: 30px;
      grid-row-gap: 30px;
      margin-top: 20px;
    }
  }
  .content-container {
    .contact-title {
      font-family: 'InterUI-Medium';
      font-size: 14px;
      color: #FFFFFF;
      line-height: 30px;
      margin: 15px 0;
    }
    .contact-flex-container {
      height: 100%;
      padding: 0;
      margin: 0;
      display: -webkit-box;
      display: -moz-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      .contact-flex-box {
        flex: 0.35;
      }
      .contact-flex-box:last-of-type {
        border-left: 2px solid #E6E6E6;
        padding-left: 35px;
      }
      .flex-container {
        height: 100%;
        padding: 0;
        margin: 0;
        display: -webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        // align-items: center;
        // justify-content: center;
      }
      .flex-box-one {
        flex: 0.2;
        margin: 0 !important;
      }
      .flex-box-two {
        flex: 2;
      }
    }
    .contact-person {
      font-family: 'InterUI-Regular';
      font-size: 14px;
      color: #9B9FBD;
      line-height: 21px;
      margin: 0;
    }
    .contact-address {
      font-family: 'InterUI-Regular';
      font-size: 14px;
      color: #9B9FBD;
      line-height: 21px;
      margin: 0;
    }
    .contact-number {
      color:  #F8B63E;
    }
    .information-title {
      font-family: 'InterUI-Bold';
      font-size: 15px;
      color: #FFFFFF;
      line-height: 24px;
    }
    .schedule-chat-button {
      padding-top: 20px;
      background: #F8B63E;
      border-radius: 5px;
      color: #FFFFFF;
      padding: 7px 40px;
      margin-top: 20px;
      border: none;
    }
    .navigation-title {
      font-family: 'InterUI-Bold';
      font-size: 15px;
      color: #FFFFFF;
      line-height: 24px;
      margin: 0;
    }
    .navigation-list {
      margin: 15px 0 0 0;
      p {
        font-family: 'InterUI-Regular';
        font-size: 14px;
        color: #9B9FBD;
        line-height: 30px;
        padding: 0;
        margin: 0;
      }
    }
    .flex-container {
      height: 100%;
      padding: 0;
      margin: 0;
      display: -webkit-box;
      display: -moz-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      // align-items: center;
      // justify-content: center;
    }
    .flex-box-one {
      flex: 0.2;
      margin-right: 40px;
    }
    .flex-box-two {
      flex: 2;
    }
  }
  .footer-mobile-container{
    display: none;
  }
  .information-title-mobile {
    display: none;
  }
  .mobile-footer {
    display: none;
  }
  .logos-mobile {
    display: none;
  }
  .desktop-footer {
    display: inline-block;
  }
  .social-media-links {
    a {
      color: #ffbb41;
    }
  }
  .company-social {
    font-size: 28px;
  }
  div>ul.list-horizontal {
      text-align: right;
        >li {
            display: inline-block;
            // margin: 0 1em 1em 0;
            padding: 0 .5em 0 0 !important;
            font-size: 2em;
        }
    }
  // New homepage media query
  @media (max-width: 768px) {
    div>ul.list-horizontal {
        text-align: center;
    }
    .logos-desktop {
        display: none;
    }
    .social-media-links {
        margin-top: 20px !important;
        margin-left: 30px !important;
    }
    ul.list-horizontal {
        padding: 0 !important;
    }
    .company-name {
      text-align: center;
    }
    .logos-desktop {
      display: none;
    }
    .logos-mobile {
      display: inline-block;
      text-align: center;
      width: 100%;
      ul.list-horizontal {
        padding: 0 !important;
      }
    }
    .mobile-footer {
      display: inline-block;
    }
    .content-container {
      .information-title-mobile {
        display: inline-block;
        font-family: "InterUI-Bold";
        font-size: 15px;
        color: #FFFFFF;
        line-height: 24px;
        margin: 0;
      }
      .information-title{
        display: none;
      }
      .schedule-chat-button {
        display: none;
      }
      .flex-box-one {
        flex: 0.8;
        margin: 0 !important;
      }
      .flex-box-one-info {
        flex: 0.3;
        margin: 0 0 15px 0 !important;
      }
      .contact-flex-box {
        margin-top: 20px;
      }
      .contact-flex-container {
        display: none;
        // height: 100%;
        // padding: 0;
        // margin: 0;
        // display: -webkit-box;
        // display: -moz-box;
        // display: -ms-flexbox;
        // display: -webkit-flex;
        // display: flex;
        flex-wrap: wrap;
        .contact-flex-box {
          flex: 0.35;
        }
        .contact-flex-box:last-of-type {
          border-left: none;
          padding-left: 0;
        }
        .flex-box-one {
          flex: 0.2;
          margin: 0 !important;
        }
        .flex-box-two {
          flex: 2;
        }
      }
    }
    .desktop-footer {
      display: none;
    }
    .blog-item-mobile {
      margin-bottom: 35px;
    }
    .blog-item-mobile:last-of-type {
      margin-bottom: 0;
    }
    .footer-mobile-container{
      display: inline-block;
    }
    .recent-post-container {
      .recent-post-title {
        font-family: 'InterUI-Bold';
        font-size: 15px;
        color: #FFFFFF;
        line-height: 24px;
        margin-top: 40px;
      }
    }
    .blog-content-mobile {
      padding-right: 10px !important;
    }
  }
}